"use client";

import { useEffect, useMemo, useState } from "react";
import {
  collection,
  getDocs,
  writeBatch,
  doc,
  deleteDoc,
} from "firebase/firestore";
import { db } from "@/app/lib/firebase";

type Filtro = {
  coluna: string;
  valor: string;
};

export function useEvents() {
  const [eventos, setEventos] = useState<any[]>([]);
  const [colunas, setColunas] = useState<string[]>([]);
  const [filtro, setFiltro] = useState<Filtro | null>(null);
  const [loading, setLoading] = useState<boolean>(false);

  // üîπ Carrega dados do Firestore (fonte oficial do dashboard)
  const carregarFirebase = async () => {
    setLoading(true);

    const ref = collection(db, "eventos");
    const snap = await getDocs(ref);
    const dados = snap.docs.map(doc => doc.data());

    setEventos(dados);
    setColunas(Object.keys(dados[0] || {}));
    setFiltro(null);

    setLoading(false);
  };

  // üîπ Salva dados no Firestore (batch ‚Üí performance)
  const salvarFirebase = async (dados: any[]) => {
    setLoading(true);

    const batch = writeBatch(db);
    const ref = collection(db, "eventos");

    dados.forEach(item => {
      const docRef = doc(ref);
      batch.set(docRef, item);
    });

    await batch.commit();

    await carregarFirebase(); // üî• reflete no dashboard ap√≥s salvar
  };

  // üîπ Limpa TODOS os dados do Firestore
  const limparFirebase = async () => {
    setLoading(true);

    const ref = collection(db, "eventos");
    const snap = await getDocs(ref);

    for (const docSnap of snap.docs) {
      await deleteDoc(docSnap.ref);
    }

    setEventos([]);
    setColunas([]);
    setFiltro(null);

    setLoading(false);
  };

  // üîπ Aplica filtro global (tabela + gr√°fico)
  const eventosFiltrados = useMemo(() => {
    if (!filtro) return eventos;

    return eventos.filter(ev =>
      String(ev[filtro.coluna]) === filtro.valor
    );
  }, [eventos, filtro]);

  // üîπ Carrega dados ao abrir o app
  useEffect(() => {
    carregarFirebase();
  }, []);

  // üîπ Limpar todos os dados
  const limparDados = async () => {
    const confirmar = window.confirm(
      "Tem certeza que deseja apagar todos os dados? Essa a√ß√£o n√£o pode ser desfeita."
    );

    if (confirmar) {
      await limparFirebase();
    }
  };

  return {
    eventos: eventosFiltrados,
    colunas,
    loading,
    setFiltro,
    carregarFirebase,
    salvarFirebase,
    limparFirebase,
  };
}
