"use client";

import { useEvents } from "@/app/hooks/useEvents";

export default function Filtros() {
  const { colunas, setFiltro } = useEvents();

  return (
    <select
      onChange={e => {
        const [coluna, valor] = e.target.value.split("|");
        if (!coluna) return;
        setFiltro({ coluna, valor });
      }}
    >
      <option value="">Filtrar por...</option>

      {colunas.map(col =>
        ["IA", "UX", "Tecnologia", "Palestra", "Workshop"].map(valor => (
          <option key={`${col}-${valor}`} value={`${col}|${valor}`}>
            {col}: {valor}
          </option>
        ))
      )}
    </select>
  );
}
