"use client";

import * as XLSX from "xlsx";
import { useState } from "react";
import { useEvents } from "@/app/hooks/useEvents";

export default function UploadExcel() {
  const { salvarFirebase } = useEvents();

  const [file, setFile] = useState<File | null>(null);
  const [status, setStatus] = useState<
    "idle" | "loading" | "success" | "error"
  >("idle");
  const [mensagem, setMensagem] = useState("");

  const processarArquivo = async () => {
    if (!file) return;

    if (!file.name.endsWith(".xlsx") && !file.name.endsWith(".xls")) {
      setStatus("error");
      setMensagem("Arquivo n√£o suportado. Envie um Excel.");
      return;
    }

    try {
      setStatus("loading");
      setMensagem("Enviando dados para o banco...");

      const buffer = await file.arrayBuffer();
      const workbook = XLSX.read(buffer);
      const sheet = workbook.Sheets[workbook.SheetNames[0]];
      const json = XLSX.utils.sheet_to_json(sheet, { defval: "" });

      await salvarFirebase(json); // üî• envia direto para o banco

      setStatus("success");
      setMensagem("Arquivo processado e salvo com sucesso!");
    } catch (err) {
      setStatus("error");
      setMensagem("Erro ao salvar os dados no banco.");
    }
  };

  function limparDados(): void {
    throw new Error("Function not implemented.");
  }

  return (
    <div>
      <input
        type="file"
        accept=".xlsx,.xls"
        onChange={e => setFile(e.target.files?.[0] || null)}
      />

      <button onClick={processarArquivo} style={{ marginTop: 8 }}>
        Processar arquivo
      </button>

      <button onClick={() => limparDados()} style={{ marginTop: 8 }}>
        Limpar dados
      </button>

      {status !== "idle" && (
        <p
          style={{
            marginTop: 8,
            color: status === "error" ? "red" : "green",
          }}
        >
          {mensagem}
        </p>
      )}
    </div>
  );
}
